<template>
  <!-- 页眉组件 -->
  <div class="header">
    <div class="hidden-md-and-down">
      <el-menu :router="true" mode="horizontal" background-color="#282626" text-color="#f8d87c">
        <el-menu-item index="10" :route="{path: '/'}">
          <el-image :src="headerimg" alt="logo" class="logo" ></el-image>
        </el-menu-item>
        <el-menu-item index="/">{{ $t('header.purchas') }}</el-menu-item>
        <el-menu-item index="/tasks">{{ $t('header.tasks') }}</el-menu-item>
        <el-menu-item index="/withdraw">{{ $t('header.withdraw') }}</el-menu-item>
        <el-menu-item index="/statistics">{{ $t('header.statistics') }}</el-menu-item>
        <el-menu-item index="/masternode">{{ $t('header.masterNode') }}</el-menu-item>
        <el-submenu index="5">
          <template slot="title">{{ $t('header.wallet') }}</template>
          <el-menu-item class="account">{{ myEth.address }}</el-menu-item>
          <el-menu-item @click="unlocakWallet()">{{ myEth.loginWords }}</el-menu-item>
          <el-menu-item index="/chat">{{ $t('header.chat') }}</el-menu-item>
          <el-menu-item index="/news">{{ $t('header.news') }}</el-menu-item>
          <el-menu-item>
            <a
              href="https://kocweb.github.io/#/"
              target="_blank"
              class="a-sytle"
            >{{ $t('header.homePage') }}</a>
          </el-menu-item>
          <el-menu-item>
            <a
              href="https://etherscan.io/address/0x4dd9961dd60feb4b83319d63fb8a6bdcdbb1c933"
              target="_blank"
              class="a-sytle"
            >{{ $t('header.exploer') }}</a>
          </el-menu-item>
        </el-submenu>
        <el-submenu index="9">
          <template slot="title">{{ $t('header.language')}}</template>
          <el-menu-item @click="switchLang('zh')">简体中文</el-menu-item>
          <el-menu-item @click="switchLang('en')">English</el-menu-item>
        </el-submenu>
      </el-menu>
    </div>

    <div class="hidden-lg-and-up">
      <el-menu
        class="el-menu-vertical-demo"
        :router="true"
        menu-trigger="click"
        mode="horizontal"
        background-color="#282626"
        text-color="#f8d87c"
      >
        <el-submenu index="11">
          <template slot="title">
            <span slot="title">{{ $t('header.trade') }}</span>
          </template>
          <el-menu-item index="/">{{ $t('header.purchas') }}</el-menu-item>
          <el-menu-item index="/tasks">{{ $t('header.tasks') }}</el-menu-item>
          <el-menu-item index="/withdraw">{{ $t('header.withdraw') }}</el-menu-item>
          <el-menu-item index="/statistics">{{ $t('header.statistics') }}</el-menu-item>
          <el-menu-item index="/masternode">{{ $t('header.masterNode') }}</el-menu-item>
        </el-submenu>
        <el-submenu index="12">
          <template slot="title">
            <span slot="title">{{ $t('header.wallet') }}</span>
          </template>
          <el-menu-item class="account">{{ myEth.address | ellipsis }}</el-menu-item>
          <el-menu-item @click="unlocakWallet()">{{ myEth.loginWords }}</el-menu-item>
          <el-menu-item index="/chat">{{ $t('header.chat') }}</el-menu-item>
          <el-menu-item index="/news">{{ $t('header.news') }}</el-menu-item>
          <el-menu-item>
            <a
              href="https://kocweb.github.io/#/"
              target="_blank"
              class="a-sytle"
            >{{ $t('header.homePage') }}</a>
          </el-menu-item>
          <el-menu-item>
            <a
              href="https://etherscan.io/address/0x4dd9961dd60feb4b83319d63fb8a6bdcdbb1c933"
              target="_blank"
              class="a-sytle"
            >{{ $t('header.exploer') }}</a>
          </el-menu-item>
        </el-submenu>
        <el-submenu index="13">
          <template slot="title">{{ $t('header.language')}}</template>
          <el-menu-item @click="switchLang('zh')">简体中文</el-menu-item>
          <el-menu-item @click="switchLang('en')">English</el-menu-item>
        </el-submenu>
      </el-menu>
    </div>
  </div>
</template>

<script>
import unlocakMetaMask from '../../plugins/metamask'
export default {
  name: 'Header',
  data () {
    return {
      isCollapse: false,
      headerimg: require('../../assets/img/logo.png'),
      metaInfo: {
        warning: 'Warning',
        loginWords: 'Login Wallet',
        aginLogin: 'Log in to your wallet again',
        metaMaskAction: 'Metamask authorization operations are too frequent, please reload',
        allowMetaMask: 'The metamask authorization request has been issued, please click metamask authorization'
      }
    }
  },
  computed: {
    myEth () {
      const address = this.$store.state.metamask.currentAccount
      const status = this.$store.state.metamask.status
      if (status !== 1) {
        return { address: address, loginWords: this.metaInfo.loginWords }
      } else {
        return {address: address, loginWords: this.metaInfo.aginLogin}
      }
    }
  },
  watch: {
    '$i18n.locale': {
      handler: function (newVal) {
        if (newVal === 'en') {
          this.metaInfo.loginWords = 'Login Wallet'
          this.metaInfo.aginLogin = 'Log in to your wallet again'
          this.metaInfo.metaMaskAction = 'Metamask authorization operations are too frequent, please reload'
          this.metaInfo.allowMetaMask = 'The metamask authorization request has been issued, please click metamask authorization'
        } else {
          this.metaInfo.loginWords = '登录钱包'
          this.metaInfo.aginLogin = '重新登录钱包'
          this.metaInfo.metaMaskAction = 'metamask授权操作过于频繁，请重新加载'
          this.metaInfo.allowMetaMask = 'metamask授权请求已发出，请点击metamask授权'
        }
      },
      immediate: true
    }
  },
  methods: {
    switchLang (lang) {
      this.$store.commit('i18n/switchLang', lang)
    },
    unlocakWallet () {
      const metamaskMark = this.$metamaskMark
      if (metamaskMark > 100) {
        this.$notify({
          title: this.metaInfo.warning,
          message: this.metaInfo.metaMaskAction,
          type: 'warning'
        })
        return
      }
      const commitMark = this.$store.state.metamask.commitMark
      if (metamaskMark - commitMark >= 2) {
        this.$notify.info({
          title: this.metaInfo.warning,
          message: this.metaInfo.allowMetaMask
        })
      } else {
        unlocakMetaMask()
      }
    }
  },
  filters: {
    ellipsis (value) {
      if (!value) return ''
      if (value.length > 8) {
        return value.slice(0, 6) + '...' + value.slice(38, 42)
      }
      return value
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../assets/variables.scss";
.header {
  .hidden-md-and-down {
    position: fixed;
    top: 0;
    z-index: 52;
    width: 100%;
    background: linear-gradient(to bottom, #2c2c2c, #242121);
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.55);
    .el-menu {
      width: 920px;
      margin: 0 auto;
      .el-menu-item {
        min-width: 100px;
        text-align: center;
      }
      .el-submenu {
        min-width: 120px;
        text-align: center;
      }
    }
  }
  .hidden-lg-and-up {
    background-color: #282626;
    .el-menu {
      width: 310px;
      margin: 0 auto;
      .el-menu-item {
        font-size: 16px;
        padding: 0;
        text-align: center;
      }
      .el-submenu {
        min-width: 90px;
        text-align: center;
      }
    }
  }
}
.el-menu-item {
  font-size: 16px;
}
.logo {
  width: 66px;
  height: 45px;
}

/* ETH间隔线 */
.account {
  border-bottom: 0.5px solid #f9d76c;
}
</style>
<style scoped>
.hidden-lg-and-up .el-submenu >>> .el-submenu__title {
  padding: 0 8px;
  font-size: 16px;
}
.el-submenu >>> .el-submenu__title {
  font-size: 16px;
}

.el-menu.el-menu--horizontal {
  border-bottom: solid 0px #ffffff;
}
.el-menu--horizontal >>> .el-menu-item.is-active {
  border-bottom: 0px solid #282727;
  color: #f8d87c;
}
.el-menu--horizontal >>> .el-submenu.is-active .el-submenu__title {
  border-bottom: 0px solid #282727;
  color: #f8d87c;
}
.el-menu--horizontal .el-menu-item:not(.is-disabled):hover {
  color: #f8d87c;
}
</style>



