<template>
  <div class="index">
    <div class="applogo hidden-lg-and-up">
     <el-image :src="logoimg" alt="logo" class="logo" @click="$router.push({path: '/'})"></el-image>
      THE KEY OF CONTRACT
    </div>
    <Header />
    <div class="content">
      <router-view />
    </div>
  </div>
</template>

<script>
import Header from '../components/common/Header'
import { mixinMeta } from '../Mixin/mixinMeta'
// import unlocakMetaMask from '../plugins/metamask'
// import Web3 from 'web3'
export default {
  name: 'Index',
  mixins: [mixinMeta],
  data () {
    return {
      logoimg: require('../assets/img/logo.png')
    }
  },
  components: {
    Header
  }
}
</script>

<style lang="scss">
@import "../assets/base.scss";
.applogo {
  font-size: 22px;
  font-family:ArialMT;
  width: 310px;
  height: 100px;
  line-height: 100px;
  margin: 0 auto;
  color: $app-name;
  white-space: nowrap;
  text-decoration: none;
  .logo {
    width: 53px;
    height: 36px;
    vertical-align: middle;
  }
}
</style>



