// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/index'
import Element from 'element-ui'
import i18n from './utils/i18n'
import './element-variables.scss'
import 'element-ui/lib/theme-chalk/display.css'
import ethConf from './lib/eth/config'
import countDown from './plugins/countDown'

countDown() // 执行倒计时
Vue.config.productionTip = false
Vue.prototype.$ethConf = ethConf()
Vue.prototype.$myProvider = null // 用于保存metamask provider
Vue.prototype.$metamaskMark = 0 // 标志 unlocakMetaMask()的执行次数，防止重复执行导致阻塞内存溢出
Vue.prototype.$myWeb3 = null // 用于保存new Web3(provider)
Vue.prototype.$myContracts = null // 与合约的交互, 存储多合约 todo 如果使用多合约，则需要修改 eth/config plugins/metamask 发送交易的页面

Vue.use(Element, {
  i18n: (key, value) => i18n.t(key, value)
})

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')



