// eth交易数据处理
import BigNumber from 'bignumber.js'

export default {
  ethToWei: function (ethNum) {
    return new BigNumber(ethNum).multipliedBy(1e18).toString()
  },
  weiToEth: function (weiNum) { // 控制精度长度
    return new BigNumber(weiNum).dividedBy(1e18).toString()
  },
  handleDate: function (ethDate) {
    let date = new Date(ethDate)
    const Y = date.getFullYear() + '-'
    const M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
    const D = date.getDate() + ' '
    const h = date.getHours() + ':'
    const m = date.getMinutes() + ':'
    const s = date.getSeconds()
    return Y + M + D + h + m + s
  },
  taskMinus: function (a, b) { // getPurchaseInfo()接口使用
    a = new BigNumber(a).multipliedBy(0.6)
    b = new BigNumber(b)
    const val = a.minus(b).toNumber()
    if (val > 0) {
      return val.toString
    } else {
      return 0
    }
  },
  ethToKoC: function (a) {
    return new BigNumber(a).multipliedBy(1000).toString()
  },
  // 市场扶持奖励，value的处理
  supportReward: function (value) {
    return new BigNumber(value).multipliedBy(0.1).toString()
  },
  // 浮点数精度调整处理
  // js具有浮点数精度丢失问题，因此使用字符串处理方式控制精度
  numFloatLen: function (number, len) { // number需要处理的数字，len处理后的精度长度
    if ((typeof number === 'number' || typeof number === 'string') && typeof len === 'number') {
      // number = parseFloat(number)
      len = parseInt(len)
      if (len > 10) {
        len = 10
      }
      if (number || parseFloat(number) === 0) { // 防止number为NaN
        let numStr = number.toString()
        let numArr = numStr.split('.')
        let newNumber = null
        if (len > 0) { // toFixed() 为了防止精度丢失，暂时只支持1-10位精度
          if (numArr.length === 2) {
            let numInt = numArr[0]
            let numFloat = numArr[1]
            if (numFloat.length >= len) {
              newNumber = parseFloat(numInt + '.' + numFloat.substring(0, len))
            } else {
              let zeroLen = len - numFloat.length
              for (let i = 1; i <= zeroLen; i++) {
                numFloat += '0'
              }
              newNumber = parseFloat(numInt + '.' + numFloat)
            }
            return newNumber.toFixed(len)
          } else { // 如果是整数，则小数部分补零
            let numInt = numArr[0]
            let numFloat = null
            for (let j = 1; j <= len; j++) {
              numFloat += '0'
            }
            newNumber = parseFloat(numInt + '.' + numFloat)
            return newNumber.toFixed(len)
          }
        } else { // len <= 0时, 返回整数
          newNumber = parseInt(numArr[0], 10)
          return newNumber
        }
      }
    } else {
      return null
    }
  }
}



