// 浏览器扩展，metamask
import store from '../store/index'
import Vue from 'vue'
import Web3 from 'web3'

export default async function () {
  Vue.prototype.$metamaskMark += 1
  // todo 电脑适配
  if (typeof window.ethereum !== 'undefined') {
    const provider = window['ethereum'] // 获取metamask全局对象
    Vue.prototype.$myProvider = provider
    const myWeb3 = new Web3(provider) // web3 不可以存储进入vuex中
    // todo url检查
    // 浏览器会忽略#标志后面的数据，所以vue如果使用hash路由模式，则location.search获取不到值,需要启用history模式;
    let url = window.location.href
    const index = url.indexOf('?to=')
    let urlParams = url.slice(index) // 获取url参数，审核是否具有邀请链接;
    let referralAddress = null // 交易数据
    if (urlParams && urlParams.substr(0, 4) === '?to=') {
      referralAddress = urlParams.substr(4) // buy接口的referralAddress
      if (referralAddress && referralAddress.length === 42) {
        store.commit('metamask/setUrlStatus', 1)
      } else {
        store.commit('metamask/setUrlStatus', 0)
      }
    } else {
      store.commit('metamask/setUrlStatus', 0)
    }
    try {
      // todo 如果使用多合约，则需要创建多个合约对象
      const contractRes = new myWeb3.eth.Contract(Vue.prototype.$ethConf.contractRes.abi, Vue.prototype.$ethConf.contractRes.address)
      const contractTeam = new myWeb3.eth.Contract(Vue.prototype.$ethConf.contractTeam.abi, Vue.prototype.$ethConf.contractTeam.address)
      const contractRec = new myWeb3.eth.Contract(Vue.prototype.$ethConf.contractRec.abi, Vue.prototype.$ethConf.contractRec.address)
      const contractTerm = new myWeb3.eth.Contract(Vue.prototype.$ethConf.contractTerm.abi, Vue.prototype.$ethConf.contractTerm.address)
      Vue.prototype.$myWeb3 = myWeb3 // 注入全局web3
      Vue.prototype.$myContracts = {contractRes, contractTeam, contractRec, contractTerm} // 注入全局contract
    } catch (error) {
      console.error('myContract的注入发生错误', error)
    }
    // store.commit('metamask/setProvider', provider) // metamask全局对象不可以保存至vuex中
    // store.commit('metamask/setNetworkVersion', provider.networkVersion)
    let isUnlocaked = true
    try {
      isUnlocaked = await provider._metamask.isUnlocked()
    } catch (error) {
      console.error('metamask实验api错误', error)
    }
    if (!isUnlocaked) {
      const status = 3
      const commitMark = store.state.metamask.commitMark + 1
      store.commit('metamask/setStatus', status)
      store.dispatch('metamask/setCurrentAccount', '') // 为了异步操作，检测白名单，使用actions
      store.commit('metamask/setCommitMark', commitMark)
      return status
    } else {
      try {
        const accounts = await provider.enable()
        const status = 1
        const commitMark = store.state.metamask.commitMark + 1
        store.dispatch('metamask/setCurrentAccount', accounts[0]) // 为了异步操作，检测白名单，使用actions
        store.commit('metamask/setStatus', status)
        store.commit('metamask/setCommitMark', commitMark)
        provider.on('accountsChanged', onAccounts) // 监听账户变动
        // provider.on('networkChanged', onNetwork) // 监听networkVersion变动
        return status
      } catch (reason) {
        const status = 2
        const commitMark = store.state.metamask.commitMark + 1
        store.commit('metamask/setStatus', status)
        store.dispatch('metamask/setCurrentAccount', '') // 为了异步操作，检测白名单，使用actions
        store.commit('metamask/setCommitMark', commitMark)
        console.error('用户拒绝了授权登陆')
        return status
      }
    }
  } else if (typeof window.web3 !== 'undefined') {
    const status = 5
    const commitMark = store.state.metamask.commitMark + 1
    store.commit('metamask/setStatus', status)
    store.dispatch('metamask/setCurrentAccount', '') // 为了异步操作，检测白名单，使用actions
    store.commit('metamask/setCommitMark', commitMark)
    return status // 用户没有安装metamask
  } else {
    const status = 4
    const commitMark = store.state.metamask.commitMark + 1
    store.commit('metamask/setStatus', status)
    store.dispatch('metamask/setCurrentAccount', '') // 为了异步操作，检测白名单，使用actions
    store.commit('metamask/setCommitMark', commitMark)
    return status // 用户没有安装metamask
  }
}

function onAccounts (accounts) { // 监听metamask切换账户更新数据，但是无法监听到用户的登出
  console.warn('账户变动：', accounts[0])
  // 账号切换，更新web3
  // Vue.prototype.$myWeb3 = new Web3(Vue.prototype.$myProvider) todo web3似乎不会失效，在最新的测试中是这样滴
  // 更新vuex中的metamask数据
  const status = 1
  const commitMark = store.state.metamask.commitMark + 1
  store.commit('metamask/setStatus', status)
  store.dispatch('metamask/setCurrentAccount', accounts[0]) // 为了异步操作，检测白名单，使用actions
  store.commit('metamask/setCommitMark', commitMark)
}

