import store from '../store/index'
import endTime from '../lib/countDown/config'

export default function startCountDown () {
  let id = setTimeout(() => {
    // 清空定时器
    clearTimeout(id)
    // todo 每次都获取新的时间，有可能会栈溢出
    let currentTime = new Date().getTime()
    // let lastTime = new Date()
    let currentCountDown = endTime - currentTime
    // 判断是否结束
    if (currentCountDown <= 0) {
      store.commit('countDown/setStatus', false)
      return
    }
    let days = Math.floor(currentCountDown / (24 * 3600 * 1000))
    let hours = Math.floor((currentCountDown - days * 24 * 3600 * 1000) / (3600 * 1000))
    let minutes = Math.floor((currentCountDown - days * 24 * 3600 * 1000 - hours * 3600 * 1000) / (60 * 1000))
    let seconds = Math.floor((currentCountDown - 1000 * (days * 24 * 3600 + hours * 3600 + minutes * 60)) / 1000)
    days = addZero(days)
    hours = addZero(hours)
    minutes = addZero(minutes)
    seconds = addZero(seconds)
    store.commit('countDown/updateCountDown', {days, hours, minutes, seconds})
    startCountDown()
  }, 1000)
}

function addZero (num) {
  if (num > 60) {
    return ''
  } else if (num < 10) {
    num = '0' + num
  }
  return num
}



