import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/index'
import unlocakMetaMask from '../plugins/metamask'
import Index from '@/view/index'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      children: [
        {
          name: 'Purchase',
          path: 'purchase',
          component: () => import('@/view/Purchase'),
          alias: '/'
        },
        {
          name: 'WhiteList',
          path: 'whitelist',
          component: () => import('@/view/whitelist')
        },
        {
          name: 'Tasks',
          path: 'tasks',
          component: () => import('@/view/tasks')
        },
        {
          name: 'Withdraw',
          path: 'withdraw',
          component: () => import('@/view/Withdraw')
        },
        {
          name: 'Statistics',
          path: 'statistics',
          component: () => import('@/view/Statistics')
        },
        {
          name: 'MasterNode',
          path: 'masternode',
          component: () => import('@/view/MasterNode')
        },
        {
          name: 'News',
          path: 'news',
          component: () => import('@/view/News')
        },
        {
          name: 'NewsDetail',
          path: 'news/newsdetail',
          component: () => import('@/view/NewsDetail')
        },
        {
          name: 'Chat',
          path: 'chat',
          component: () => import('@/view/Chat')
        },
        {
          name: 'NotFound',
          path: '/404',
          component: () => import('@/components/common/NotFound')
        }
      ]
    },
    {// 路由最下方为404页面
      path: '*',
      redirect: '/404'
    }
  ]
})

// 全局前置守卫
router.beforeEach(async (to, from, next) => {
  const status = store.state.metamask.status
  if (status === 0 && Vue.prototype.$metamaskMark === 0) {
    unlocakMetaMask() // todo 如果用户没有选择，不处理！大量切换路由，可能会炸；unlocakMetaMask()执行次数过多，增加标志位解决
  }

  // todo 新的需求 用户没有安装metamask和使用不合法的直推地址，不可以进入tasks、withdraw、statistics、masternode
  const whiteListLimit = ['Purchase', 'Tasks', 'Withdraw', 'Statistics', 'MasterNode']
  const countDownStatus = true // 是否处于白名单时期
  const fromQuery = from.query
  const fromParam = fromQuery.to
  const toParam = to.query.to
  if (fromParam && !toParam) {
    if (whiteListLimit.includes(to.name)) {
      if (countDownStatus) {
        next({
          path: '/whitelist',
          query: fromQuery
        })
      } else { // 使用else强逻辑可以绝对控制，若是，不适用else，而是之恶在结尾加上一个 next(),那么在路由到当前路由时则不会再次强制路由跳转
        next({
          path: to.path,
          query: fromQuery
        })
      }
    } else {
      if (to.name === 'WhiteList' && !countDownStatus) { // 防止白名单时期结束，用户修改url进入白名单页面
        next({
          path: '/',
          query: fromQuery
        })
      } else {
        next({
          path: to.path,
          query: fromQuery
        })
      }
    }
  } else {
    if (whiteListLimit.includes(to.name)) {
      if (countDownStatus) {
        next({
          path: '/whitelist'
        })
      } else { // 使用else强逻辑可以绝对控制，若是，不适用else，而是之恶在结尾加上一个 next(),那么在路由到当前路由时则不会再次强制路由跳转
        next()
      }
    } else {
      if (to.name === 'WhiteList' && !countDownStatus) { // 防止白名单时期结束，用户修改url进入白名单页面
        if (!fromParam && toParam) { // 解决，白名单时期结束，用户使用白名单时期的直推地址进入，则保留直推地址进入purchase页面
          next({
            path: '/purchase',
            query: to.query
          })
        } else {
          next({
            path: '/purchase'
          })
        }
      } else {
        next()
      }
    }
  }
})

// 全局后置守卫
router.afterEach(async (to, from) => {
  // const status = store.state.metamask.status
  // if (status === 0 && Vue.prototype.$metamaskMark === 0) {
  //   await unlocakMetaMask() // todo 如果用户没有选择，不处理！大量切换路由，可能会炸；unlocakMetaMask()执行次数过多，增加标志位解决
  // }
})

export default router



