// contract event handle and save
import Vue from 'vue'
import ethUtils from '../../utils/ethUtils'
const state = { // event name type
  buy: [],
  purchaseTasksInfo: {},
  recommendRecord: [],
  personalStatistics: {}, // 个人统计
  contractStatistics: {}, // 合约统计
  userBalance: null, // withdraw页面 user余额
  allWhiteList: null,
  rankOfRecommend: [], // 直推排名
  straightInviteAddress: [], // your team 所有的直推下级地址
  teamData: [], // your team 最终注入页面的数据
  teamDataLen: 0, // 记录已经获取的straightInviteAddress的长度，以便与最新的straightInviteAddress比对
  teamDataAccount: '', // 标记档期teamData存储的是哪一个数据，应该使用一个对象管理teamData的，但是，都写到这了。。。还是不改了
  teamLock: {},
  terminatorInfo: [],
  achieveTerminator: false
}

const mutations = {
  SET_EVENT_BUY (state, eventData) {
    const returnValues = eventData.returnValues
    const transactionHash = eventData.transactionHash
    const date = ethUtils.handleDate(returnValues.buyTime.toNumber() * 1000) // 返回的参数buyTime是bignumber类型才能这么用
    const tx = transactionHash
    let eth = ethUtils.weiToEth(returnValues.ethAmount)
    eth = ethUtils.numFloatLen(eth, 4)
    const address = returnValues.user
    state.buy.unshift({date, tx, eth, address})
  },
  SET_EVENT_TOBEWHITE (state, eventData) {
    const userAddress = eventData.user
    state.allWhiteList.unshift(userAddress)
  },
  SET_ACHIEVETERMINATOR (state, eventData) {
    state.achieveTerminator = eventData
  },
  SET_PURCHASETASKSINFO (state, info) {
    state.purchaseTasksInfo = info
  },
  SET_RECOMMENDRECORD (state, record) {
    state.recommendRecord = record
  },
  SET_PERSONALSTATISTICS (state, personalStatistics) {
    state.personalStatistics = personalStatistics
  },
  SET_CONTRACTSTATISTICS (state, contractStatistics) {
    state.contractStatistics = contractStatistics
  },
  SET_USERBALANCE (state, userBalance) {
    state.userBalance = userBalance
  },
  SET_ALLWHITELIST (state, allWhiteList) { // 第一次加载所有whiteList
    state.allWhiteList = allWhiteList
  },
  SET_RANKOFRECOMMEND (state, rankOfReommend) {
    state.rankOfRecommend = rankOfReommend
  },
  SET_STRAIGHTINVITEADDRESS (state, straightInviteAddress) {
    state.straightInviteAddress = straightInviteAddress
  },
  SET_TEAMDATALEN (state, teamDataLen) { // 初始化team的数组长度
    state.teamDataLen = teamDataLen
  },
  SET_TEAMDATA (state, teamData) {
    // console.log('vuex存储的teamData的数据', teamData)
    if (teamData.length === 0) {
      state.teamData = []
    } else {
      teamData.forEach(el => {
        state.teamData.push(el)
      })
    }
  },
  SET_TEAMDATACCOUNT (state, account) {
    state.teamDataAccount = account
  },
  SET_TEAMLOCK (state, lock) {
    state.teamLock = lock
  },
  SET_TERMINATORINFO (state, terminatorInfo) {
    state.terminatorInfo = terminatorInfo
  }
}

const actions = {
  // 最好做一个统一接口
  async startListenEvent ({dispatch, commit}, data) { // data = { contract, eventName } todo 是否有特殊情况，待定
    // console.warn('开始执行今天events')
    const contract = data.contract
    const eventName = data.eventName
    // 网络判断
    const myProvider = Vue.prototype.$myProvider
    if (myProvider && myProvider.networkVersion === Vue.prototype.$ethConf.networkVersion) {
      Vue.prototype.$ethConf.subscribeMark[eventName] = true // 改变订阅状态
      contract.events[eventName]() // 没有使用回调函数，而是监听，看后续变化
        .on('data', function (event) { // 接收到新的事件时触发，参数为事件对象
          // 不同的事件不同的处理
          if (eventName === 'Buy') {
            const eventData = {}
            eventData.returnValues = event.returnValues
            eventData.transactionHash = event.transactionHash
            commit('SET_EVENT_BUY', eventData)
            // dispatch('getUserBalance'), 余额更新待定，时间或者buy触发的次数决定
          }
          if (eventName === 'TobeWhitelistAddress') {
            const eventData = event.returnValues
            commit('SET_EVENT_TOBEWHITE', eventData)
          }
          if (eventName === 'AchieveTerminator') {
            const eventData = true
            commit('SET_ACHIEVETERMINATOR', eventData)
          }
        })
        .on('changed', function (event) { // 当事件从区块链上移除是触发，该事件对象将被添加额外的属性"removed: true"

        })
        .on('error', function (error) {
          console.error(error)
        })
    }
  },
  // 数据查询，展示数据的统一vuex处理
  async getPurchaseTasksInfo ({commit}) {
    const data = {}
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) { // 过滤，非指定网络交易
      const contractRes = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractRes // Resonance合约地址 todo 标记 引用合约
      if (contractRes) {
        data.from = myProvider.selectedAddress
        contractRes.methods.getPurchaseTasksInfo().call({
          from: data.from
        }, function (error, result) {
          if (error) {
            console.error(error)
          } else {
            let tasksPurchase = ethUtils.weiToEth(result.refeTopAmount)
            tasksPurchase = ethUtils.numFloatLen(tasksPurchase, 4)
            let hadPurchase = ethUtils.weiToEth(result.ethAmount)
            hadPurchase = ethUtils.numFloatLen(hadPurchase, 4)
            let needSpend = ethUtils.taskMinus(tasksPurchase, hadPurchase)
            needSpend = ethUtils.numFloatLen(needSpend, 4)
            const tasksAdress = result.refeTopAddress
            let lockStraight = ethUtils.weiToEth(result.lockStraight)
            lockStraight = ethUtils.numFloatLen(lockStraight, 4)
            commit('SET_PURCHASETASKSINFO', {tasksPurchase, hadPurchase, needSpend, tasksAdress, lockStraight})
          }
        })
      }
    }
  },
  async getRecommendRecord ({commit}) {
    const data = {}
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractRec = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractRec // Recommend合约地址  todo 标记 引用合约
      if (contractRec) {
        data.from = myProvider.selectedAddress
        contractRec.methods.getRecommendRecord().call({
          from: data.from
        }, function (error, result) {
          if (error) {
            console.error(error)
          } else { // 返回的数据是四个数组
            const record = []
            const straightTime = result.straightTime
            const ethAmount = result.ethAmount
            const refeAddress = result.refeAddress
            const supported = result.supported // boolean值，是否已支持
            for (let i = 0; i < straightTime.length; i++) {
              // 时间处理
              const date = straightTime[i]
              const oldTime = ethUtils.handleDate(date.toNumber() * 1000) // 将获取的时间戳s转换为毫秒在new Date
              const nowTime = new Date()
              const timeDiff = nowTime.getTime() - date.toNumber() * 1000
              const leftDays = Math.floor(timeDiff / (24 * 3600 * 1000)) // 毫秒转天数
              // ethAmount处理
              let eth = ethUtils.weiToEth(ethAmount[i])
              // eth = ethUtils.numFloatLen(eth, 4)
              // refeAddress 处理
              const address = refeAddress[i]
              // supported处理
              const isSupported = supported[i]
              // 索引
              const index = i
              record.unshift({index, oldTime, eth, address, leftDays, isSupported})
            }
            commit('SET_RECOMMENDRECORD', record)
          }
        })
      }
    }
  },
  // 个人中心统计
  async getPersonalStatistics ({commit}) {
    const data = {}
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractRes = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractRes
      if (contractRes) {
        data.from = myProvider.selectedAddress
        contractRes.methods.getPersonalStatistics().call({
          from: data.from
        }, function (error, result) {
          if (error) {
            console.error(error)
          } else {
            let holdings = ethUtils.weiToEth(result.holdings) // 投资数量
            holdings = ethUtils.numFloatLen(holdings, 4)
            let dividends = ethUtils.weiToEth(result.dividends) // 未出局静态收益
            dividends = ethUtils.numFloatLen(dividends, 4)
            const invites = result.invites // 直推邀请人数
            const level = result.level // 等级
            let afterFounds = ethUtils.weiToEth(result.afterFounds) // 复投基金
            afterFounds = ethUtils.numFloatLen(afterFounds, 4)
            let referralRewards = ethUtils.weiToEth(result.referralRewards) // 直推奖励
            referralRewards = ethUtils.numFloatLen(referralRewards, 4)
            let teamRewards = ethUtils.weiToEth(result.teamRewards) // 团队奖励
            teamRewards = ethUtils.numFloatLen(teamRewards, 4)
            let nodeRewards = ethUtils.weiToEth(result.nodeRewards) // 节点奖励
            nodeRewards = ethUtils.numFloatLen(nodeRewards, 4)
            commit('SET_PERSONALSTATISTICS', {holdings, dividends, invites, level, afterFounds, referralRewards, teamRewards, nodeRewards})
          }
        })
      }
    }
  },
  // 获取所有的直推下级地址
  async getStraightInviteAddress ({commit}) {
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractRes = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractRes
      if (contractRes) {
        const fromAddress = myProvider.selectedAddress
        contractRes.methods.getStraightInviteAddress().call({
          from: fromAddress
        }, function (error, result) {
          if (error) {
            console.error(error)
          } else {
            const straightInviteAddress = result // 只返回一个值时，直接返回值本身，而不是一个对象
            commit('SET_STRAIGHTINVITEADDRESS', straightInviteAddress)
          }
        })
      }
    }
  },
  // 根据getStraightInviteAddress返回值调用，查询指定账户中的holdings和level数据
  async getUserStatistics ({commit}, userAddressArr) { // userAddressArr 账户地址
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractRes = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractRes
      if (contractRes) {
        let addressLen = userAddressArr.length
        let begin = 0
        for (let j = 0; j < 100; j++) {
          if (addressLen > 10) {
            const teamData = []
            for (let i = 0; i < 10; i++, begin++) { // 每次最多获取十条数据
              addressLen--
              const userAddress = userAddressArr[begin]
              contractRes.methods.getPersonalStatistics().call({
                from: userAddress
              }, function (error, result) {
                if (error) {
                  console.error(error)
                } else {
                  // console.log('getUserStatistics交易的返回值', result)
                  let holdings = ethUtils.weiToEth(result.holdings) // 投资数量
                  holdings = ethUtils.numFloatLen(holdings, 4)
                  const level = result.level // 等级
                  teamData.push({
                    address: userAddress, // todo 这里可能有异步回调问题，回调时begin可能并不是一开始的那个值;如果出现，可以尝试定义const变量控制,经过尝试const可以和回调绑定
                    holdings: holdings,
                    level: level
                  })
                  if (i === 9) {
                    commit('SET_TEAMDATA', teamData)
                  }
                }
              })
            }
          } else {
            const teamData = []
            for (let i = 0; i < addressLen; i++, begin++) {
              const userAddress = userAddressArr[begin]
              contractRes.methods.getPersonalStatistics().call({
                from: userAddress
              }, function (error, result) {
                if (error) {
                  console.error(error)
                } else {
                  // console.log('getUserStatistics交易的返回值', result)
                  let holdings = ethUtils.weiToEth(result.holdings) // 投资数量
                  holdings = ethUtils.numFloatLen(holdings, 4)
                  const level = result.level // 等级
                  teamData.push({
                    address: userAddress, // todo 这里可能有异步回调问题，回调时begin可能并不是一开始的那个值
                    holdings: holdings,
                    level: level
                  })
                  if (i === (addressLen - 1)) {
                    commit('SET_TEAMDATA', teamData)
                  }
                }
              })
              if (i === (addressLen - 1)) {
                j = 1000
              }
            }
          }
        }
      }
    }
  },
  // 合约统计 resonances合约地址
  async getContractStatistics ({commit}) {
    const data = {}
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractRes = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractRes
      const myWeb3 = Vue.prototype.$myWeb3
      let eggPool = await myWeb3.eth.getBalance(Vue.prototype.$ethConf.eggPoolAdress) // todo 固定的eth账户地址，会给定;在config中配置
      eggPool = ethUtils.numFloatLen(ethUtils.weiToEth(eggPool), 4)
      let nodePool = await myWeb3.eth.getBalance(Vue.prototype.$ethConf.nodePoolAdress) // todo 固定的eth账户地址，会给定;在config中配置
      nodePool = ethUtils.numFloatLen(ethUtils.weiToEth(nodePool), 4)
      if (contractRes) {
        data.from = myProvider.selectedAddress
        contractRes.methods.contractStatistics().call({
          from: data.from
        }, function (error, result) {
          if (error) {
            console.error(error)
          } else {
            let recommendRankPool = ethUtils.weiToEth(result.recommendRankPool) // 直推排序奖池
            recommendRankPool = ethUtils.numFloatLen(recommendRankPool, 4)
            let terminatorPool = ethUtils.weiToEth(result.terminatorPool) // 终结者奖池
            terminatorPool = ethUtils.numFloatLen(terminatorPool, 4)
            commit('SET_CONTRACTSTATISTICS', {recommendRankPool, terminatorPool, eggPool, nodePool})
          }
        })
      }
    }
  },
  // 直推排名
  async listRankOfRecommend ({commit}) {
    const data = {}
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractRes = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractRes
      if (contractRes) {
        data.from = myProvider.selectedAddress
        contractRes.methods.listRankOfRecommend().call({
          from: data.from
        }, function (error, result) {
          if (error) {
            console.error(error)
          } else {
            const straightSortAddress = result._straightSort // 直推地址 [10]
            const inviteNumber = result._inviteNumber // 直推人数 [10]
            const rankOfRecommend = []
            for (let j = 0; j < inviteNumber.length; j++) {
              if (inviteNumber[j].toNumber() !== 0) {
                const rankObject = {}
                rankObject.straightSortAddress = straightSortAddress[j]
                rankObject.inviteNumber = inviteNumber[j].toNumber()
                rankOfRecommend.push(rankObject)
              }
            }
            commit('SET_RANKOFRECOMMEND', rankOfRecommend)
          }
        })
      }
    }
  },
  // 获取withdraw页面余额信息
  async getUserBalance ({commit}) {
    const data = {}
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractRes = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractRes
      if (contractRes) {
        data.from = myProvider.selectedAddress
        contractRes.methods.getUserBalance().call({
          from: data.from
        }, function (error, result) {
          if (error) {
            console.error(error)
          } else {
            let staticBalance = ethUtils.weiToEth(result.staticBalance) // 静态余额奖励
            staticBalance = ethUtils.numFloatLen(staticBalance, 4)
            let recommendBalance = ethUtils.weiToEth(result.recommendBalance) // 直推奖励
            recommendBalance = ethUtils.numFloatLen(recommendBalance, 4)
            let teamBalance = ethUtils.weiToEth(result.teamBalance) // 团队奖励
            teamBalance = ethUtils.numFloatLen(teamBalance, 4)
            let terminatorBalance = ethUtils.weiToEth(result.terminatorBalance) // 终结者分红奖励
            terminatorBalance = ethUtils.numFloatLen(terminatorBalance, 4)
            let nodeBalance = ethUtils.weiToEth(result.nodeBalance) // 节点分红奖励
            nodeBalance = ethUtils.numFloatLen(nodeBalance, 4)
            // todo eth兑换usdt价格暂时不做处理，跨域和翻墙，火币还限制访问频率
            commit('SET_USERBALANCE', {staticBalance, recommendBalance, teamBalance, terminatorBalance, nodeBalance})
          }
        })
      }
    }
  },
  async isLockWithdraw ({commit}) {
    const data = {}
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractRes = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractRes
      if (contractRes) {
        data.from = myProvider.selectedAddress
        contractRes.methods.isLockWithdraw().call({
          from: data.from
        }, function (error, result) {
          if (error) {
            console.error(error)
          } else {
            const isLock = result.isLock // teamReward是否被锁定
            let lockTime = result.lockTime // 锁定开始时间, 如果有值，应该是s
            if (isLock) {
              lockTime = ethUtils.handleDate(lockTime.toNumber() * 1000)
            }
            commit('SET_TEAMLOCK', {isLock, lockTime})
          }
        })
      }
    }
  },
  // 获取白名单列表
  async getAdminWhitelistAddress ({commit}) { // 可以直接看到
    const allWhiteList = []
    const adminAddress = Vue.prototype.$ethConf.adminAddress
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractTeam = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractTeam // TeamReward合约地址
      if (contractTeam) {
        for (let i = 0; i < adminAddress.length; i++) {
        // todo 尝试调用call方法，不配置call()参数
          // todo 这里可能会有异步问题
          const arrData = await contractTeam.methods.getAdminWhitelistAddress(adminAddress[i]).call({})
          if (arrData) {
            for (let i = 0; i < arrData.length; i++) {
              allWhiteList.unshift(arrData[i])
            }
          }
          if (i === (adminAddress.length - 1)) {
            commit('SET_ALLWHITELIST', allWhiteList) // 这边估计有异步问题，需要await解决
          }
          // contractTeam.methods.getAdminWhitelistAddress(adminAddress[i]).call({}, function (error, result) {
          //   if (error) {
          //     console.error(error)
          //   } else {
          //     const arrData = result
          //     if (arrData) {
          //       // for (let i = 0; i < arrData.length; i++) {
          //       //   allWhiteList.unshift(arrData[i])
          //       // }
          //       arrData.forEach(el => {
          //         allWhiteList.unshift(el)
          //       })
          //     }
          //     if (i === (adminAddress.length - 1)) {
          //       commit('SET_ALLWHITELIST', allWhiteList) // 这边估计有异步问题，需要await解决
          //     }
          //   }
          // })
        }
      }
    }
  },
  // 终结者奖励处理
  // 获取最近一次获得终结者区块高度和奖励的所有用户地址和上一次获奖数量
  async getLatestTerminatorInfo ({commit}) {
    const terminatorInfo = []
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractTerm = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractTerm
      if (contractTerm) {
        contractTerm.methods.getLatestTerminatorInfo().call({}, function (error, result) {
          if (error) {
            console.error(error)
          } else {
            const blockNum = result.blockNumber
            const addressArray = result.addressArray // [50],定长数组，没有数据的元素，可能是默认填充0x00000000...
            const amountArray = result.amountArray // [50], 定长数组，没有数据的元素，可能默认填充0
            for (let i = 0; i < amountArray.length; i++) {
              if (parseFloat(ethUtils.weiToEth(amountArray[i])) === 0) {
                i = 50
              } else {
                const terminator = {}
                terminator.blockNum = blockNum.toNumber()
                terminator.address = addressArray[i]
                terminator.amount = ethUtils.numFloatLen(ethUtils.weiToEth(amountArray[i]), 4)
                terminatorInfo.push(terminator)
              }
            }
            commit('SET_TERMINATORINFO', terminatorInfo)
          }
        })
      }
    }
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}



