// 倒计时
// import endTime from '../../lib/countDown/config'

const state = {
  days: '',
  hours: '',
  minutes: '',
  seconds: '',
  // status: endTime > (new Date().getTime()) // true: 倒计时持续中， false：倒计时结束 todo 是否强制进入白名单界面
  status: true // true: 倒计时持续中， false：倒计时结束 todo 是否强制进入白名单界面
}

const mutations = {
  updateCountDown (state, {days, hours, minutes, seconds}) {
    state.days = days
    state.hours = hours
    state.minutes = minutes
    state.seconds = seconds
  },
  setStatus (state, status) {
    state.status = status
  }
}

export default {
  namespaced: true,
  state,
  mutations
}



